                        class="fa fa-chevron-left"
                        @click="$router.go(-1)"
                    ></span>
                    歌单
                </div>
                <div
                    class="bg"
                    :style="{
                        backgroundImage: 'url(' + PlayList.coverImgUrl + ')'
                    }"
                ></div>
                <div class="wrap">
                    <div class="content">
                        <div class="left-image">
                            <img :src="PlayList.coverImgUrl" alt="" />
                            <span class="icon">歌单</span>
                            <span class="num">
                                <i class="fa fa-headphones"></i>
                                {{ translatePlayCount(PlayList.playCount) }}
                            </span>
                        </div>
                        <div class="right-text">
                            <div class="name">{{ PlayList.name }}</div>
                            <div class="author">
                                <div class="portrait" v-if="PlayList">
                                    <img
                                        :src="PlayList.creator.avatarUrl"
                                        alt=""
                                    />
                                    <span class="fa fa-star-o"></span>
                                </div>
                                <span>{{ PlayList.creator.nickname }}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="playlist-content">
                <div class="label">
                    标签：
                    <span
                        v-for="(item, index) in PlayList.creator.expertTags"
                        :key="index"
                        >{{ item }}</span
                    >
                </div>
                <div class="description">
                    简介:
                    <span>{{ PlayList.description }}</span>
                </div>
            </div>
            <div class="playlist-title">歌曲列表</div>
            <ul>
                <SongListItem
                    v-for="(song, index) in PlayList.tracks"
                    :key="index"
                    :item="song"
                    :index="index"
                    :items="PlayList.tracks"
                    :isSequence="true"
                    :isShowQuality="false"
                    @translate-currentid="$emit('translate-currentid', $event)"
                ></SongListItem>
            </ul>
