<script>
import SongListItem from "@/components/SongListItem.vue";

export default {
    name: "PlayList",
    components: {
        SongListItem
    },
    data: function() {
        return {
            PlayList: [],
            descriptions: [],
            loading: false
        };
    },
    methods: {
        getPlayListDetail(id) {
            this.loading = true;
            this.axios
                .get("https://music.kele8.cn/playlist/detail?id=" + id)
                .then(res => {
                    // console.log(res);
                    ({ playlist: this.PlayList } = res.data);
                    this.loading = false;
                });
        },
        handleSequence(index) {
            // console.log("xxx");

            if (index < 9) {
                return "0" + (index + 1);
            } else {
                return index + 1;
            }
        },
        translatePlayCount(n) {
            if (n >= 100000000) {
                return (n / 100000000).toFixed(2) + "亿";
            }
            if (n >= 10000) {
                return (n / 10000).toFixed(1) + "万";
            }
        }
    },
    beforeRouteEnter(to, from, next) {
        next(vm => {
            // console.log(vm);
            vm.getPlayListDetail(to.query.id);
        });
    }
};
</script>

<style lang="less">
.playlist {
    height: 100vh;
    position: relative;
    svg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .header {
        width: 100%;
        height: 180px;
        overflow: hidden;
        position: relative;
        padding: 30px 10px 30px 15px;
        .go-back {
            position: absolute;
            top: 5px;
            left: 5px;
            color: rgb(236, 235, 235);
            z-index: 2;
            font-size: 12px;
            span {
                font-weight: 300;
                // margin-right: 15px;
                display: inline-block;
            }
        }
        .bg {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 230px;
            background-size: cover;
            background-position: 50% center;
            background-repeat: no-repeat;
            filter: blur(20px);
            transform: scale(1.5);
            z-index: 1;
        }
        .wrap {
            position: relative;
            z-index: 2;
            width: 100%;
            height: 100%;
            .content {
                display: flex;
                width: 100%;
                height: 100%;
                .left-image {
                    position: relative;
                    display: inline-block;
                    margin-right: 20px;
                    img {
                        width: 126px;
                    }
                    span.icon {
                        width: 35px;
                        height: 17px;
                        border-top-right-radius: 17px;
                        border-bottom-right-radius: 17px;
                        background: rgba(217, 48, 48, 0.8);
                        color: white;
                        font-size: 10px;
                        line-height: 17px;
                        text-align: center;
                        position: absolute;
                        top: 15px;
                        left: 0;
                    }
                    span.num {
                        position: absolute;
                        top: 0;
                        right: 5px;
                        font-size: 10px;
                        color: white;
                        i {
                            font-size: 12px;
                            margin-right: 2px;
                            color: white;
                            line-height: 16px;
                        }
                    }
                }
                .right-text {
                    flex: 1;
                    display: inline-block;
                    div.name {
                        font-size: 17px;
                        color: white;
                        margin-bottom: 15px;
                    }
                    div.author {
                        height: 30px;
                        line-height: 30px;
                        .portrait {
                            width: 35px;
                            height: 30px;
                            display: inline-block;
                            margin-right: 5px;
                            position: relative;
                            img {
                                width: 30px;
                                height: 30px;
                                vertical-align: middle;
                                border-radius: 50%;
                            }
                            span {
                                position: absolute;
                                bottom: 0;
                                right: 0;
                                width: 12px;
                                height: 12px;
                                text-align: center;
                                line-height: 12px;
                                background: rgb(250, 174, 10);
                                color: white;
                                border-radius: 50%;
                                font-size: 10px;
                            }
                        }

                        span {
                            vertical-align: middle;
                            color: hsla(0, 0%, 100%, 0.7);
                            font-size: 14px;
                        }
                    }
                }
            }
        }
    }
    .playlist-content {
        background: #f8f8f8;
        padding-top: 5px;

        .label {
            height: 30px;
            padding-left: 15px;
            line-height: 30px;
            color: #666666;
            font-size: 14px;
            padding-bottom: 10px;
            box-sizing: content-box;
            // margin-bottom: 5px;
            span {
                display: inline-block;
                height: 22px;
                line-height: 22px;
                border-radius: 5px;
                border: 1px solid rgba(0, 0, 0, 0.1);
                margin-right: 20px;
                padding: 0 5px;
                font-size: 12px;
            }
        }
        .description {
            height: 55px;
            padding-bottom: 18px;
            overflow: hidden;
            color: #666666;
            font-size: 14px;
            padding: 0 10px 15px 15px;
            box-sizing: content-box;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            span {
                line-height: 35px;
            }
        }
    }
    .playlist-title {
        height: 23px;
        line-height: 23px;
        padding: 0 10px;
        font-size: 12px;
        color: #666666;
        background: #eeeff0;
    }
}
</style>
